import gql from "graphql-tag";
import qraphql from "@/apollo-client";

function toWithId(params: string[]) {
  return params.map((el, index) => ({
    value: index + 1,
    text: el,
  }));
}

class AccidentModel {
  id = null;
  static defaultData = {
    generalInfo: {
      num: "",
      note: "",
      date: "",
      time: "",
      category: "",
      view: "",
    },
    roadCondition: {
      view: "",
      condition: "",
      illumination: "",
    },

    vehicle: {
      model: "",
      technicalPassport: "",
      plateNumber: "",
      engineNumber: "",
      bodyNumber: "",
      yearManufacture: "",
      location: "",
      typeCar: 0,
      typeDrive: 0, // тип привода
      typeCargo: 0,
      steeringArrangement: "",
      color: "",
      trailerPresence: false, // наличие прицепа
      technicalFault: 0,
    },

    people: {
      gender: "",
      birthday: "",
      category: "",
      location: "",
      losses: "",
      violation: "",
      belt: "",
      experience: "",
    },
  };

  options = {
    generalInfo: {
      category: toWithId(["с участием ТС и пешехода", "с участием одного ТС", "столкновешие между ТС", "столкновешие между ТС и подвижным составом ж-д", "прочие"]),
    },
    vehicle: {
      typeCar: [
        { value: 10, text: "автобус" },
        { value: 20, text: "автомобиль грузовой" },
        { value: 30, text: "автомобиль легковой" },
        { value: 40, text: "полуприцеп" },
        { value: 50, text: "прицеп" },
        { value: 60, text: "мотоцикл" },
        { value: 61, text: "мотоцикл без коляски" },
        { value: 62, text: "мотоцикл с коляской" },
        { value: 70, text: "мотороллер" },
        { value: 80, text: "мотоколяска" },
      ],
      typeDrive: toWithId(["передний", "задний", "полный"]),
      typeCargo: toWithId(["обычный", "негабаритный / тяжеловесный", "опасный"]),
      color: [
        { value: 1, text: "белый" },
        { value: 5, text: "оттенки белого" },
        { value: 10, text: "желтый" },
      ],
      technicalFault: toWithId(["тормозной системы", "рулевого управления", "отсоединение колеса"]),
      trailerPresence: ["да", "нет"],
    },
    people: {
      gender: toWithId(["м", "ж"]),
      category: toWithId(["водитель", "пассажир", "пешеход", "велосипедист"]),
      losses: toWithId(["ранен", "погиб", "мат. ущерб"]),
    },
  };

  getValueByOptions(value: any, options: { value: number; text: string }[]) {
    return options.find(el => el.value === value)?.text;
  }
  getValueByOptionsBool(value: any, options: { value: number; text: string }[]) {
    return value ? options[0] : options[1];
  }

  async load(paramId: any) {
    const extractId = paramId.id;

    if (this.id !== null || extractId !== undefined) {
      this.id = this.id === null ? Number(extractId) : this.id;
      const response = await qraphql.query({
        query: gql`
          query($id: Int!) {
            accident_crash(where: { id: { _eq: $id } }) {
              vehicle
              people
              generalInfo
              geom
            }
          }
        `,
        variables: {
          id: this.id,
        },
      });

      const {
        data: { accident_crash },
      } = response;
      if (accident_crash.length <= 0) {
        this.id = null;
      }
      return accident_crash.length > 0 ? accident_crash[0] : [];
    } else {
      return [];
    }
  }

  async save(vehicle: any[], people: any[], generalInfo: object, geom: object) {
    const id = this.id;

    const objects = {
      id,
      vehicle,
      people,
      generalInfo,
      geom,
    };

    if (id === null) {
      delete objects.id;
    }

    const response = await qraphql.mutate({
      mutation: gql`
        mutation($objects: [accident_crash_insert_input!]!) {
          insert_accident_crash(on_conflict: { constraint: crash_pkey, update_columns: [vehicle, people, generalInfo, geom] }, objects: $objects) {
            affected_rows
            returning {
              id
            }
          }
        }
      `,
      variables: { objects },
    });

    const {
      data: {
        insert_accident_crash: { affected_rows, returning },
      },
    } = response;

    this.id = returning.id;

    return affected_rows;
  }
}

export { AccidentModel };
