import { AccidentModel } from "@/lib/ModelCRUDAccident";
import VOl from "@/lib/v-ol.vue";

@Component({
  components: {
    VOl
  }
})
export default class App extends Vue {
  model: AccidentModel;
  generalInfo = Object.assign(
    {},
    JSON.parse(JSON.stringify(AccidentModel.defaultData.generalInfo))
  );
  generalInfoDateMenu = false;
  generalInfoTimeMenu = false;
  snackbar = false;
  load = true;
  editedIndex = -1;
  created() {
    console.log("created");

    this.model = new AccidentModel();

    this.editedItem = Object.assign(
      {},
      JSON.parse(JSON.stringify(AccidentModel.defaultData))
    );
  }
  async mounted() {
    const data = await this.model.load(this.$route.params);
    Object.keys(data).forEach(key => {
      const refObject = this[key];
      if (Array.isArray(refObject)) {
        this[key] = data[key];
      } else if (typeof refObject === "object") {
        this[key] = data[key];
      }
    });
    this.load = false;
  }
  newAccident() {
    this.created();
  }
  headers = {
    vehicle: [
      {
        text: "Марка, модель, модификация ТС",
        value: "model",
        align: "center"
      },
      { text: "Тип ТС", value: "typeCar" },
      { text: "Техпаспорт", value: "technicalPassport" },
      { text: "Номерной знак", value: "plateNumber" },
      { text: "№ двигателя", value: "engineNumber" },
      { text: "№ кузова", value: "bodyNumber" },
      { text: "Гоп выпуска", value: "yearManufacture" },
      { text: "Подробнее", value: "data-table-expand" },
      { text: "Действия", value: "actions", sortable: false }
    ],
    people: [
      { text: "Пол", value: "gender" },
      { text: "Дата рождения", value: "birthday" },
      { text: "Категория участника", value: "category" },
      { text: "Место жительства", value: "location" },
      { text: "тяжесть", value: "losses" },
      { text: "Нарушение ПДД", value: "violation" },
      { text: "Ремень", value: "belt" }
    ]
  };
  dialog = {
    vehicle: false,
    people: false,
    peopleBirthday: false,
    peopleExperience: false
  };
  editedItem = {};
  expanded = [];
  vehicle = [];
  people = [];
  geom = null;
  async save() {
    const result = await this.model.save(
      this.vehicle,
      this.people,
      this.generalInfo,
      this.geom
    );
    if (result > 0) {
      this.snackbar = true;
    }
  }
  closeItem() {
    this.editedItem = Object.assign(
      {},
      JSON.parse(JSON.stringify(AccidentModel.defaultData))
    );
    Object.keys(this.dialog).forEach(key => {
      this.dialog[key] = false;
    });
  }
  saveItem(name: string) {
    if (this.editedIndex > -1) {
      this.$set(this[name], this.editedIndex, this.editedItem[name]);
    } else {
      this[name].push(this.editedItem[name]);

      console.log(this.people, this.editedItem);
    }
    this.closeItem();
  }
  editItem(item: any, name: string) {
    this.editedIndex = this[name].indexOf(item);
    this.editedItem[name] = Object.assign({}, item);
    this.dialog[name] = true;
  }
  deleteItem(item: any, name: string) {
    const index = this[name].indexOf(item);
    const result = confirm("Уверен, что удалить это лучшее решение?");
    if (result) {
      this.$delete(this[name], index);
    }
  }

  peopleDriverExpretience(item) {
    const { category, experience } = item;
    const val = this.model.options.people.category.find(
      el => el.value === category
    );
    const exp =
      experience !== "" ? `с ${new Date(experience).getFullYear()} года` : "";
    return category === 1
      ? `${val.text}, стаж ${exp !== "" ? exp : "не задан"}`
      : val !== null
      ? val.text
      : "";
  }

  cardOption = [
    {
      id: 1,
      val: "Карточка направляется впервые"
    },
    {
      id: 2,
      val: "Карточка изывается"
    },
    {
      id: 3,
      val: "Карточка с изменениями (дополн)"
    }
  ];

  roadView = [
    { id: 1, val: "Асфальтобетонное" },
    { id: 2, val: "Цементробетонное" },
    {
      id: 3,
      val: "Поверхн. обработка по а/бетонному или ц/бетонному покрытию"
    },
    { id: 4, val: "Щебеночное (гравий), обработанное вяжущими материалами" },
    { id: 5, val: "Щебеночное (гравий, шлам)" },
    { id: 6, val: "Мостовые" },
    { id: 7, val: "Грунтовое" },
    { id: 8, val: "Прочее" }
  ];

  roadCondition = [
    { id: 1, val: "Сухая" },
    { id: 2, val: "Мокрая" },
    {
      id: 3,
      val: "Гололедица"
    },
    { id: 4, val: "Снежный накат" },
    { id: 5, val: "Посыпанная противогололёдным материалом при гололедице" },
    {
      id: 6,
      val: "Посыпанная противогололёдным материалом при снежном накате"
    },
    { id: 7, val: "Заснеженная" },
    { id: 8, val: "Загрязненная" },
    { id: 9, val: "Обработана вяжуш. материалами" },
    { id: 10, val: "Свежеулож. поверхн. обработка" },
    { id: 11, val: "Прочее" }
  ];

  get viewOption() {
    const { category } = this.generalInfo;

    const v2 = [
      { value: 1, text: "Опрокидывание" },
      { value: 2, text: "Наезд на препятствие" },
      { value: 3, text: "Наезда на животное" }
    ];
    const v3 = [
      { value: 1, text: "Столкновение с ударом сзади" },
      { value: 2, text: "Столкновение на пересечении дорог или поворотах" },
      { value: 3, text: "Лобовое столкновение" },
      { value: 4, text: "Попутное столкновение" },
      { value: 5, text: "Столкновение со стоящим ТС" },
      { value: 6, text: "Наезда на велосипедиства" },
      { value: 7, text: "Наезад на гужевой транспорт" }
    ];

    return Number(category) === 2 ? v2 : Number(category) === 3 ? v3 : [];
  }

  formatMTSYearManifacture(data: string) {
    return new Date(data).getFullYear();
  }
}
